import { defineStore } from 'pinia'

export const useGlobalStore = defineStore('global', {
  state: () => {
    return {
      isLeftSidebarMinimized: false,
      isRightSidebarMinimized: true,
      filteringGuidanceTimestamp: undefined,
      currentProjectName: 'Fake Dataset',
      currentProjectIndex: 0,
    }
  },

  actions: {
    toggleSidebar() {
      this.isLeftSidebarMinimized = !this.isLeftSidebarMinimized
    },
    updateFilteringGuidanceTimestamp() {
      this.filteringGuidanceTimestamp = Date.now()
    },
  },
})
