<template>
  <VaSidebar v-model="writableVisible" :width="sidebarWidth" :color="color" minimized-width="0">
    <VaAccordion v-model="value" multiple>
      <VaCollapse>
        <template #header="{ value: isCollapsed }">
          <VaSidebarItem
            :active="true"
            :active-color="activeColor"
            text-color="primary"
            role="button"
            hover-opacity="0.10"
          >
            <VaSidebarItemContent class="py-2 pr-2 pl-2">
              <VaSidebarItemTitle class="flex items-center leading-5 font-semibold cursor-auto">
                <VaButton
                  preset="secondary"
                  @click="isLeftSidebarMinimized = !isLeftSidebarMinimized"
                  class="mr-2 cursor-pointer"
                  icon="keyboard_arrow_left"
                  v-tooltip="'Close left panel'"
                >
                </VaButton>
                Project: {{ currentProjectName }}
              </VaSidebarItemTitle>
            </VaSidebarItemContent>
          </VaSidebarItem>
        </template>
      </VaCollapse>
    </VaAccordion>
    <div class="flex w-full p-1">
      <h3 class="sidebar-title mb-2 mt-1"><VaIcon name="storage" size="22px" />Database</h3>
    </div>
    <div class="flex w-full p-1">
      <VaInput
        v-model="filter"
        placeholder="Search data..."
        clearable
        class="mr-3 grow-0 basis-24"
        v-tooltip="'Coming soon: search in your dataset using any metadata'"
      />
    </div>

    <TreeView
      ref="treeView"
      id="project-browser"
      v-model="treeDataModel"
      :filterMethod="customFilterMethod"
      :model-defaults="modelDefaults"
      @contextmenu="onContextMenu"
      @treeNodeCheckboxChange="onTreeNodeSelectionChanged"
      @treeNodeRadioChange="onTreeNodeLayerSelectionChanged"
    />

    <VaDivider />
    <div class="flex w-full p-1" v-if="uniqueDates.length > 0">
      <h3 class="sidebar-title mb-2"><VaIcon name="filter_alt" size="22px" />Filter by date</h3>
    </div>
    <div class="w-full date-slider" v-if="uniqueDates.length > 0">
      <VaSlider
        v-model="dateRange"
        pins
        range
        :step="1"
        :min="0"
        :max="uniqueDates.length - 1"
        track-label-visible
        :track-label="processTrackLabel"
        v-tooltip="'Coming soon: filter visible data in your dashboard by date'"
      />
    </div>
  </VaSidebar>
</template>

<style lang="scss">
@import '@grapoza/vue-tree/css';

.x-flip {
  transform: scaleX(-100%);
}

.sidebar-title {
  font-weight: bold;
}

.date-slider {
  padding-left: 60px;
  padding-right: 60px;
}

#project-browser.grtv-wrapper {
  .grtvn-self-text,
  .grtvn-self-label {
    /* margin-left: 10px; */
  }

  .grtvn-children-wrapper {
    margin-left: 20px;
  }
}

.va-file-upload--dropzone {
  overflow: visible;
}
</style>

<script lang="ts">
import { defineComponent, watch, ref, computed, nextTick } from 'vue'
import { useRoute } from 'vue-router'

import { useI18n } from 'vue-i18n'
import { useToast, useColors, VaIcon } from 'vuestic-ui'

import { TreeView } from '@grapoza/vue-tree'

import ColorScales from '@components/ColorScales.vue'

import VaIconMenuCollapsed from '@components/icons/VaIconMenuCollapsed.vue'
import navigationRoutes, { type INavigationRoute } from './NavigationRoutes'
import ContextMenu from '@imengyu/vue3-context-menu'

import { useProjectBrowserStore } from '@stores/project-browser'
import { useLkpDataStore } from '@stores/geoscientific-project'
import { v4 as uuidv4 } from 'uuid'
import { storeToRefs } from 'pinia'
import { useGlobalStore } from '@stores/global-store'

export default defineComponent({
  name: 'Sidebar',
  components: {
    TreeView,
    ColorScales,
    VaIconMenuCollapsed,
  },
  props: {
    visible: { type: Boolean, default: true },
    mobile: { type: Boolean, default: false },
  },
  emits: ['update:visible'],
  inject: {
    emitter: 'emitter',
  },
  setup: (props, { emit }) => {
    const { getColor, colorToRgba } = useColors()
    const route = useRoute()
    const { t } = useI18n()

    const value = ref<boolean[]>([])

    const writableVisible = computed({
      get: () => props.visible,
      set: (v: boolean) => emit('update:visible', v),
    })

    const isActiveChildRoute = (child: INavigationRoute) => route.name === child.name

    const routeHasActiveChild = (section: INavigationRoute) => {
      if (!section.children) {
        return route.path.endsWith(`${section.name}`)
      }

      return section.children.some(({ name }) => route.path.endsWith(`${name}`))
    }

    const setActiveExpand = () =>
      (value.value = navigationRoutes.routes.map((route: INavigationRoute) => routeHasActiveChild(route)))

    const sidebarWidth = computed(() => (props.mobile ? '100vw' : '280px'))
    const color = computed(() => getColor('background-secondary'))
    const activeColor = computed(() => colorToRgba(getColor('focus'), 0.1))

    const iconColor = (route: INavigationRoute) => (routeHasActiveChild(route) ? 'primary' : 'secondary')
    const textColor = (route: INavigationRoute) => (routeHasActiveChild(route) ? 'primary' : 'textPrimary')
    const arrowDirection = (state: boolean) => (state ? 'va-arrow-up' : 'va-arrow-down')

    watch(() => route.fullPath, setActiveExpand, { immediate: true })

    const collapseIconColor = computed(() => getColor('textInverted'))

    const globalStore = useGlobalStore()

    const { isLeftSidebarMinimized, currentProjectName } = storeToRefs(globalStore)
    const dataStore = useLkpDataStore()

    return {
      writableVisible,
      sidebarWidth,
      value,
      color,
      activeColor,
      navigationRoutes,
      routeHasActiveChild,
      isActiveChildRoute,
      t,
      iconColor,
      textColor,
      arrowDirection,
      isLeftSidebarMinimized,
      currentProjectName,
      dataStore,
    }
  },
  data() {
    const dataStore = useLkpDataStore()
    const projectBrowserStore = useProjectBrowserStore()
    let fakeDataTreeStructure = projectBrowserStore.structure
    console.debug(fakeDataTreeStructure)

    const populateIds = function (nodes: Array<Object>, n: number) {
      nodes.forEach((node) => {
        node.id = n
        n++
        if (node.children !== undefined) {
          n = populateIds(node.children, n)
        }
      })
      return n
    }

    populateIds(fakeDataTreeStructure, 1)
    console.debug('Project browser data model:')
    console.debug(fakeDataTreeStructure)

    const uniqueDates = dataStore.uniqueMeasureDates
    console.debug('project date list', uniqueDates)

    return {
      filter: '',
      isFilterCaseSensitive: false,
      treeDataModel: fakeDataTreeStructure,
      uniqueDates,
      dateRange: [0, uniqueDates.length - 1],
      importFileInput: [],
    }
  },
  computed: {
    customFilterMethod() {
      return this.isFilterCaseSensitive ? (node, filterText, key) => node[key].includes(filterText) : undefined
    },
  },
  mounted() {
    this.emitter.on('lkp-leaflet-map-interpolated-layer-added', this.onInterpolatedLayerAdded)
    this.emitter.on('lkp-database-changed', this.refreshDateFilter)
    this.emitter.on('lkp-database-changed', this.resetDisplayState)

    this.resetDisplayState()
  },
  unmounted() {
    this.emitter.off('lkp-leaflet-map-interpolated-layer-added', this.onInterpolatedLayerAdded)
    this.emitter.off('lkp-database-changed', this.refreshDateFilter)
    this.emitter.off('lkp-database-changed', this.resetDisplayState)
  },
  methods: {
    onContextMenu(e: MouseEvent) {
      //prevent the browser's default menu
      e.preventDefault()
      console.debug('Context menu triggered...')

      const target = e.target as HTMLElement
      //console.debug(target)
      const nodeElement = target.closest('li[role=treeitem]')
      //console.debug(nodeElement)
      let node = undefined
      if (nodeElement) {
        const nodeAtbId = nodeElement.getAttribute('id') // Looks like "project-browser-XX"
        node = this.findTreeNodeByHtmlId(nodeAtbId)
      }

      if (node === undefined) {
        console.warn('Unable to find node data to open context menu', nodeElement)
        return
      }

      console.debug(node)

      let contextMenuItems = []
      if (node.data.actions) {
        // if (node.data.actions.openInMap) {
        //   contextMenuItems.push({
        //     label: 'Show in map',
        //     onClick: () => {
        //       this.emitShowInMap(node)
        //     },
        //   })
        // }
        if (node.data.actions.interpolateInMap) {
          contextMenuItems.push({
            label: 'Interpolate in map',
            onClick: () => {
              this.emitInterpolateInMap(node)
            },
          })
        }
      }

      //show your menu
      if (contextMenuItems.length > 0) {
        ContextMenu.showContextMenu({
          x: e.x,
          y: e.y,
          items: contextMenuItems,
        })
      }
    },
    onTreeNodeSelectionChanged(target, event) {
      console.debug('Tree node toggle', target, event)
      if (target.state.input.value) {
        this.emitShowInMap(target)
      } else {
        this.emitHideInMap(target)
      }
    },
    onTreeNodeLayerSelectionChanged(target, event) {
      console.debug('Tree node radio toggle', target, event)
      this.emitShowBaseLayerInMap(target)
    },
    async onInterpolatedLayerAdded(params) {
      console.debug('New interp layer added: create node in tree view', params)

      params.treeViewNode.data.children.push({
        id: params.layerData.nodeId,
        label: 'Interpolated',
        category: 'interpolated-layer',
        actions: { openInMap: true },
      })

      await nextTick()

      this.checkNodeWithId(params.layerData.nodeId)
      this.expandNodeWithId(params.treeViewNode.data.id)
    },
    checkNodeWithId(id: number | string) {
      const metaModelNode = this.findMetaModelNodeById(id)
      if (metaModelNode) {
        metaModelNode.state.input.value = true
      } else {
        console.warn('Unable to find node to check', id)
      }
    },
    expandNodeWithId(id: number | string) {
      const metaModelNode = this.findMetaModelNodeById(id)
      if (metaModelNode) {
        metaModelNode.state.expanded = true
      } else {
        console.warn('Unable to find node to expand', id)
      }
    },
    processTrackLabel(value, _) {
      return this.uniqueDates[value].dateString
    },
    modelDefaults(node) {
      const baseDefaults = {
        expandable: true,
        draggable: false,
        state: {
          expanded: false,
        },
      }

      const nodeData = this.findDataNodeById(node.id)
      //console.debug(data)

      const nodeToExpand = [
        'Input Data',
        'Catographic Data',
        'Geochemical Data',
        'GA5000',
        'Variotec',
        'Monitoring',
        'LAB',
      ]
      let overrideConfig = { ...baseDefaults }
      if (node.children !== undefined && node.children.length > 0 && nodeToExpand.indexOf(node.label) != -1) {
        overrideConfig.state.expanded = true
      }

      if (nodeData.actions !== undefined && nodeData.actions.openInMap !== undefined) {
        overrideConfig.input = {
          type: 'checkbox',
        }
        overrideConfig.state.input = {
          value: false,
          disabled: false,
        }
      }

      if (nodeData.actions !== undefined && nodeData.actions.showBaseLayer !== undefined) {
        overrideConfig.input = {
          type: 'radio',
          name: 'radio1',
        }
        if (node.label === 'Topography') {
          overrideConfig.input.isInitialRadioGroupValue = true
        }
      }

      return overrideConfig
    },
    findTreeNodeByHtmlId(htmlId: string) {
      const treeViewId = this.$refs.treeView.$el.getAttribute('id')
      // htmlId looks like "project-browser-XX"
      // Remove "project-browser-" and convert to integer
      const nodeId = htmlId.slice(treeViewId.length + 1)
      return this.findTreeNodeById(nodeId)
    },
    findTreeNodeById(nodeId: number | string) {
      const matches = this.$refs.treeView.getMatching((current) => current.data.id == nodeId)
      return matches.length > 0 ? matches[0] : undefined
    },
    findDataNodeById(nodeId: number | string) {
      const recursiveSearchFunc = (nodeList) => {
        for (const node of nodeList) {
          if (node.id === nodeId) return node

          if (node.children !== undefined) {
            const nodeFound = recursiveSearchFunc(node.children)
            if (nodeFound !== undefined) return nodeFound
          }
        }

        return undefined
      }

      return recursiveSearchFunc(this.treeDataModel)
    },
    findMetaModelNodeById(nodeId: number | string) {
      const recursiveSearchFunc = (nodeList) => {
        for (const node of nodeList) {
          if (node.data.id === nodeId) return node

          if (node.childMetaModels !== undefined) {
            const nodeFound = recursiveSearchFunc(node.childMetaModels)
            if (nodeFound !== undefined) return nodeFound
          }
        }

        return undefined
      }

      return recursiveSearchFunc(this.$refs.treeView.metaModel)
    },
    emitShowInMap(node: Object) {
      this.emitter.emit('lkp-show-in-map', node)
    },
    emitHideInMap(node: Object) {
      this.emitter.emit('lkp-hide-in-map', node)
    },
    emitShowBaseLayerInMap(node: Object) {
      this.emitter.emit('lkp-show-base-layer-in-map', node)
    },
    emitInterpolateInMap(node: Object) {
      this.emitter.emit('lkp-interpolate-in-map', node)
    },
    refreshDateFilter() {
      const dataStore = useLkpDataStore()
      this.uniqueDates = dataStore.uniqueMeasureDates
      console.debug('New date list', this.uniqueDates)
      this.dateRange = [0, this.uniqueDates.length - 1]
    },
    cleanupDataTree() {
      const recursiveCleanupFunc = (nodeList) => {
        for (let i = nodeList.length - 1; i >= 0; i--) {
          const node = nodeList[i]
          if (node.category === 'interpolated-layer') {
            console.debug('Remove node', node)
            nodeList.splice(i, 1)
          } else if (node.children !== undefined) {
            recursiveCleanupFunc(node.children)
          }
        }
      }

      recursiveCleanupFunc(this.treeDataModel)
    },
    resetDisplayState() {
      const ga5000_h2_id = 25
      setTimeout(() => {
        this.cleanupDataTree()
        const node = this.findTreeNodeById(ga5000_h2_id)
        if (node !== undefined) {
          this.emitShowInMap(node)
          this.checkNodeWithId(ga5000_h2_id)
        }
      }, 500)
    },
  },
})
</script>
