import { defineStore } from 'pinia'
import { LkpData, Location, Cluster, Measurement, Category, GroupBy } from '../data/types'
import { v4 as uuidv4 } from 'uuid'
import { FeatureCollection, Feature, Polygon, Position } from 'geojson'
import Papa from 'papaparse'
import { color } from 'd3-color'
import { useColorScale } from '@stores/color-scale'
import { useGlobalStore } from '@stores/global-store'
import kriging from './kriging.js'

export const useLkpDataStore = defineStore('lkpData', {
  state: () => {
    const projectName = 'French Brittany'
    const author = 'Aloïse'
    const creationDate = '2024-09-15T09:33:04'
    const numLocations = 20
    const numClusters = 5
    const locationZoneLat = [47.56, 48.9]
    const locationZoneLng = [-4.7, -1.4]
    const soilTypes = ['Argile', 'Humus', 'Silt']
    const fakeDates = [
      Date.parse('2024-09-15T09:33:04'),
      Date.parse('2024-09-16T09:33:04'),
      Date.parse('2024-10-15T09:33:04'),
      Date.parse('2025-09-15T09:33:04'),
    ]

    // generate locations
    const locations: Location[] = Array.from({ length: numLocations }, (_, i) => ({
      id: uuidv4(),
      name: `Sample ${i + 1}`,
      coordinates: {
        lat: Math.random() * (locationZoneLat[1] - locationZoneLat[0]) + locationZoneLat[0],
        lng: Math.random() * (locationZoneLng[1] - locationZoneLng[0]) + locationZoneLng[0],
      },
      soilType: soilTypes[Math.floor((Math.random() * soilTypes.length) % soilTypes.length)],
    }))
    console.debug(locations)

    // generate clusters
    const clusters: Cluster[] = Array.from({ length: numClusters }, (_, i) => ({
      id: uuidv4(),
      name: `Cluster ${i + 1}`,
      locations: [],
    }))
    locations.forEach((location, i) => {
      const clusterIndex = Math.floor((Math.random() * numClusters) % numClusters)
      clusters[clusterIndex].locations.push(location.id)
    })
    console.debug(clusters)

    // generate measurements
    const measurements: Measurement[] = new Array()
    let counter = 0
    locations.forEach((location, i) => {
      const randValueH2 = Math.random() * 300
      const randValueH2GA = Math.random() * 300
      const randValueHe = Math.random() * 10
      measurements.push({
        id: uuidv4(),
        name: `Measure H2 ${counter++}`,
        category: `H2-Labo`,
        element: `H2`,
        tool: `Labo`,
        locationId: location.id,
        description: 'This is a description...',
        acquisitionDate: fakeDates[Math.floor((Math.random() * fakeDates.length) % fakeDates.length)],
        value: randValueH2,
        valueClass: randValueH2 > 200 ? 4 : randValueH2 > 100 ? 3 : randValueH2 > 50 ? 2 : randValueH2 > 10 ? 1 : 0,
        units: ['ppm'],
      })
      measurements.push({
        id: uuidv4(),
        name: `Measure H2 ${counter++}`,
        category: `H2-GA5000`,
        element: `H2`,
        tool: `GA5000`,
        locationId: location.id,
        description: 'This is a description...',
        acquisitionDate: fakeDates[Math.floor((Math.random() * fakeDates.length) % fakeDates.length)],
        value: randValueH2GA,
        valueClass:
          randValueH2GA > 200 ? 4 : randValueH2GA > 100 ? 3 : randValueH2GA > 50 ? 2 : randValueH2GA > 10 ? 1 : 0,
        units: ['ppm'],
      })
      measurements.push({
        id: uuidv4(),
        name: `Measure He ${counter++}`,
        category: `He-Labo`,
        element: `He`,
        tool: `Labo`,
        locationId: location.id,
        description: 'This is a description...',
        acquisitionDate: fakeDates[Math.floor((Math.random() * fakeDates.length) % fakeDates.length)],
        value: randValueHe,
        valueClass: randValueHe > 5 ? 1 : 0,
        units: ['ppm'],
      })
      measurements.push({
        id: uuidv4(),
        name: `Measure H2 ${counter++}`,
        category: `H2-Monitoring`,
        element: `H2`,
        tool: `Monitoring`,
        locationId: location.id,
        description: 'This is a description...',
        acquisitionDate: fakeDates[Math.floor((Math.random() * fakeDates.length) % fakeDates.length)],
        value: [Array.from({ length: 150 }, (_, i) => Math.random() * 300), Array.from({ length: 150 }, (_, i) => i)],
        valueClass: 4,
        units: ['ppm', 'seconds'],
      })
      measurements.push({
        id: uuidv4(),
        name: `Measure Thorium ${counter++}`,
        category: `Th-MS1000`,
        element: `Th`,
        tool: `MS1000`,
        locationId: location.id,
        description: 'This is a description...',
        acquisitionDate: fakeDates[Math.floor((Math.random() * fakeDates.length) % fakeDates.length)],
        value: Math.random() * 100,
        valueClass: 0,
        units: ['ppm'],
      })
    })
    console.debug(measurements)

    return {
      currentDataset: 0,
      datasets: [
        {
          projectName,
          isFakeDataset: true,
          author,
          creationDate,
          locations,
          clusters,
          measurements,
          locationZoneLat,
          locationZoneLng,
        },
        {
          projectName,
          isFakeDataset: false,
          author,
          creationDate,
          locations: [],
          clusters: [],
          measurements: [],
          locationZoneLat,
          locationZoneLng,
        },
      ],
    }
  },
  getters: {
    currentProject(state): LkpData {
      return state.datasets[state.currentDataset]
    },
    uniqueMeasureDates(): Object[] {
      return this.currentProject.measurements
        .reduce(function (acc, measure: Measurement) {
          const acquisitionDate = new Date(measure.acquisitionDate)
          if (isNaN(acquisitionDate.valueOf())) return acc
          let dateFound = acc.find((el) => el.date.getTime() === acquisitionDate.getTime())
          return (
            dateFound === undefined
              ? acc.push({
                  dateString: acquisitionDate.toLocaleDateString(),
                  date: acquisitionDate,
                })
              : acc,
            acc
          )
        }, [] as Object[])
        .sort((a, b) => {
          return a.date - b.date
        })
    },
    categories(state): Category[] {
      return state.currentProject.measurements.reduce(function (acc, curr) {
        let cat = acc.find(
          (category) => category.shortName.localeCompare(curr.category, undefined, { sensitivity: 'accent' }) === 0,
        )
        // console.log(curr)
        return (
          cat
            ? ++cat.occurence
            : acc.push({
                shortName: curr.category,
                fullName: `${curr.category} (${curr.units.join(', ')})`,
                units: curr.units,
                valueDimension: curr.units.length,
                occurence: 1,
              }),
          acc
        )
      }, [] as Category[])
    },
    elements(): Object[] {
      return this.categories.reduce(function (acc, category) {
        const splitedName = category.shortName.split('-')
        const elementName = splitedName[0]
        let elementFound = acc.find(
          (el) => el.name.localeCompare(elementName, undefined, { sensitivity: 'accent' }) === 0,
        )
        return (
          elementFound === undefined
            ? acc.push({
                name: elementName,
              })
            : acc,
          acc
        )
      }, [] as Object[])
    },
    tools(): Object[] {
      return this.categories.reduce(function (acc, category) {
        const splitedName = category.shortName.split('-')
        const toolName = splitedName[1]
        let toolFound = acc.find((el) => el.name.localeCompare(toolName, undefined, { sensitivity: 'accent' }) === 0)
        return (
          toolFound === undefined
            ? acc.push({
                name: toolName,
              })
            : acc,
          acc
        )
      }, [] as Object[])
    },
    soilTypes(): Object[] {
      return this.currentProject.locations.reduce(function (acc, location) {
        const soilTypeName = location.soilType !== undefined ? location.soilType : 'Unknown'
        let soilTypeFound = acc.find(
          (el) => el.name.localeCompare(soilTypeName, undefined, { sensitivity: 'accent' }) === 0,
        )
        return (
          soilTypeFound === undefined
            ? acc.push({
                name: soilTypeName,
              })
            : acc,
          acc
        )
      }, [] as Object[])
    },
    groupBy(): GroupBy[] {
      return [
        {
          name: 'Cluster',
          key: 'clusters.id',
        },
        {
          name: 'Class',
          key: 'clusters.id',
        },
        {
          name: 'Soil',
          key: 'clusters.id',
        },
        //{
        //  name: 'Location',
        //  key: 'location.id',
        //},
      ]
    },
  },
  actions: {
    applyFilter(filteringObject: LkpData) {
      if (typeof filteringObject === undefined) return this.currentProject

      function removeOrphans(project: LkpData) {
        // Remove locations not belonging to a cluster
        project.locations = project.locations.filter((location: Location) =>
          project.clusters.find((cluster: Cluster) => cluster.locations.indexOf(location.id) !== -1),
        )

        // Remove measurements not belonging to a location
        project.measurements = project.measurements.filter((measure: Measurement) =>
          project.locations.find((location: Location) => location.id === measure.locationId),
        )
      }

      let filteredProject = {} as LkpData
      for (const key in this.currentProject) {
        // If key is NOT filtered, keep it as it is
        if (!(key in filteringObject)) {
          filteredProject[key] = this.currentProject[key]
          continue
        }

        // This key is filtered.
        // We only filter arrays
        if (Array.isArray(this.currentProject[key])) {
          if (!Array.isArray(filteringObject[key])) {
            console.warn(
              `Filter must be an array, ${typeof filteringObject[key]} given. This filter is skipped.`,
              filteringObject[key],
            )
            filteredProject[key] = this.currentProject[key]
            continue
          }
          const filteredValue = this.currentProject[key].filter((elem) =>
            filteringObject[key].some((filterObject) =>
              Object.entries(filterObject).every(([key, val]) =>
                typeof val === 'string' && key != 'id'
                  ? val.localeCompare(elem[key], undefined, { sensitivity: 'accent' }) === 0
                  : elem[key] === val,
              ),
            ),
          )
          filteredProject[key] = filteredValue
          //console.debug(filteredValue)
        } else {
          filteredProject[key] = this.currentProject[key]
        }
      }

      removeOrphans(filteredProject)
      //console.debug(filteredProject)

      return filteredProject as LkpData
    },
    measurementsByIds(ids: string[]): Measurement[] {
      return this.currentProject.measurements.reduce(function (arr, measure) {
        return ids.indexOf(measure.id) != -1 ? arr.push(measure) : arr, arr
      }, [] as Measurement[])
    },
    measurementById(id: string): Measurement | undefined {
      return this.currentProject.measurements.find(function (measure) {
        return measure.id === id
      })
    },
    groupMeasurementsByCluster(category: string): Object {
      const me = this
      return this.measurementsByCategory(category).reduce(function (obj, measure) {
        const cluster = me.clusterByLocationId(measure.locationId)
        if (obj[cluster.id] === undefined) {
          obj[cluster.id] = []
        }
        return obj[cluster.id].push(measure), obj
      }, {})
    },
    locationById(id: string): Location | undefined {
      return this.currentProject.locations.find((value) => value.id === id)
    },
    locationsByCluster(cluster: Cluster): Location[] {
      return this.currentProject.locations.reduce(function (arr, location) {
        return cluster.locations.indexOf(location.id) != -1 ? arr.push(location) : arr, arr
      }, [] as Location[])
    },
    clusterById(id: string): Cluster | undefined {
      return this.currentProject.clusters.find((value) => value.id === id)
    },
    clusterByLocationId(id: string): Cluster | undefined {
      return this.currentProject.clusters.find((cluster) => cluster.locations.indexOf(id) !== -1)
    },
    clusterColor(id: string): String {
      const index = this.currentProject.clusters.findIndex((c) => c.id === id)
      if (index === -1) return color('#DDDDDD').formatHex8()
      const colorStore = useColorScale()
      const normalizedValue = index / this.currentProject.clusters.length
      return color(colorStore.cluster(normalizedValue)).formatHex8()
    },
    valueColorByClass(element: string, value: number | number[][]): String {
      const valueClass = this.classFromElementValue(element, value)
      return this.classColor({
        valueClass,
        element,
      })
    },
    classColor(measure: Measurement): String {
      const colorStore = useColorScale()
      const val = measure.valueClass
      let colors = colorStore.h2Class
      switch (measure.element) {
        case 'H2':
          colors = colorStore.h2Class
          break
        case 'He':
          colors = colorStore.heClass
          break
        case 'CO':
          colors = colorStore.coClass
          break
        case 'O2':
          colors = colorStore.o2Class
          break
        case 'CO2':
          colors = colorStore.co2Class
          break
        case 'CH4':
          colors = colorStore.ch4Class
          break
        case 'N2':
          colors = colorStore.n2Class
          break
        default:
          break
      }
      const normalizedValue = Math.min(val, colors.length - 1)
      return color(colors[normalizedValue]).formatHex8()
    },
    soilColor(name: string): String {
      const index = this.soilTypes.findIndex(
        (s) => s.name.localeCompare(name, undefined, { sensitivity: 'accent' }) === 0,
      )
      if (index === -1) return color('#DDDDDD').formatHex8()
      const colorStore = useColorScale()
      const normalizedValue = index / this.soilTypes.length
      return color(colorStore.soil(normalizedValue)).formatHex8()
    },
    measurementsByCategory(category: string): Measurement[] {
      return this.currentProject.measurements.reduce(function (arr, measure) {
        return (
          measure.category.localeCompare(category, undefined, { sensitivity: 'accent' }) === 0
            ? arr.push(measure)
            : arr,
          arr
        )
      }, [] as Measurement[])
    },

    clusterBoundaries(id: string) {
      const cluster = this.clusterById(id)
      if (cluster === undefined) {
        const { locationZoneLat, locationZoneLng } = this.currentProject
        const [minLat, maxLat] = locationZoneLat
        const [minLng, maxLng] = locationZoneLng
        return {
          minLat,
          maxLat,
          minLng,
          maxLng,
        }
      }

      const locations = this.locationsByCluster(cluster)

      return {
        minLat: Math.min(...locations.map((loc) => loc.coordinates.lat)),
        maxLat: Math.max(...locations.map((loc) => loc.coordinates.lat)),
        minLng: Math.min(...locations.map((loc) => loc.coordinates.lng)),
        maxLng: Math.max(...locations.map((loc) => loc.coordinates.lng)),
      }
    },

    createGridByCluster(category: string): FeatureCollection<Polygon> {
      const clusterizedMeasurments = this.groupMeasurementsByCluster(category)
      console.debug(clusterizedMeasurments)

      const cellSizeLat = this.currentProject.isFakeDataset ? 0.05 : 0.0025
      const cellSizeLng = this.currentProject.isFakeDataset ? 0.1 : 0.005

      const features: Feature<Polygon>[] = []
      for (const clusterId in clusterizedMeasurments) {
        const measurements = clusterizedMeasurments[clusterId]
        const boundaries = this.clusterBoundaries(clusterId)
        // console.debug(boundaries)
        const grid = this.createGridKriging(measurements, boundaries, cellSizeLat, cellSizeLng)
        // console.debug(grid)
        features.push(...grid.features)
      }

      return {
        type: 'FeatureCollection',
        features,
      }
    },

    createGridKriging(measurements, boundaries, cellSizeLat, cellSizeLng) {
      let { minLat, maxLat, minLng, maxLng } = boundaries
      minLat -= cellSizeLat
      maxLat += cellSizeLat
      minLng -= cellSizeLng
      maxLng += cellSizeLng
      const category = measurements[0].category
      const element = measurements[0].element

      const numRows = Math.max(1, Math.ceil(Math.abs(maxLat - minLat) / cellSizeLat))
      const numCols = Math.max(1, Math.ceil(Math.abs(maxLng - minLng) / cellSizeLng))

      var model = 'exponential'
      var sigma2 = 0,
        alpha = 100
      var x = []
      var y = []
      var t = []

      // create a fake dataset where t is 100 when lat is east
      // for (var i = 0; i < 100; i++) {
      //   x[i] = -180 + Math.random() * 360
      //   y[i] = -90 + Math.random() * 180
      //   t[i] = x[i] > 0 ? 100 : 0
      // }
      // var fitModel = kriging.train(t, x, y, model, sigma2, alpha)
      // console.debug(kriging.predict(0, 10, fitModel))

      const features: Feature<Polygon>[] = []

      const gridValues: number[][] = Array.from({ length: numRows }, () => Array(numCols).fill(undefined))

      for (const measure of measurements) {
        const location = this.locationById(measure.locationId)
        if (location === undefined) continue
        const { lat, lng } = location.coordinates
        const i = Math.floor((lat - minLat) / cellSizeLat)
        const j = Math.floor((lng - minLng) / cellSizeLng)
        // console.debug(lat, lng, i, j)
        if (gridValues[i][j] === undefined) {
          gridValues[i][j] = measure.value
        } else {
          gridValues[i][j] = Math.max(gridValues[i][j], measure.value)
        }
      }
      //console.debug(gridValues)

      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          if (gridValues[i][j] !== undefined) {
            x.push(i)
            y.push(j)
            t.push(gridValues[i][j])
          }
        }
      }

      // console.debug(t, x, y, model, sigma2, alpha)

      const fitModel = t.length > 1 ? kriging.train(t, x, y, model, sigma2, alpha) : undefined

      //const interpolatedValues = this.inverseDistanceWeighted(gridCells, numRows, numCols, 8)
      //console.debug(interpolatedValues)

      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          const lat1 = minLat + i * cellSizeLat
          const lat2 = lat1 + cellSizeLat
          const lng1 = minLng + j * cellSizeLng
          const lng2 = lng1 + cellSizeLng

          const coordinates: Position[] = [
            [lng1, lat1],
            [lng1, lat2],
            [lng2, lat2],
            [lng2, lat1],
            [lng1, lat1],
          ]

          let value = fitModel !== undefined ? kriging.predict(i, j, fitModel) : gridValues[i][j]

          if (value !== undefined) {
            const feature: Feature<Polygon> = {
              type: 'Feature',
              properties: { value: value, name: category, element, color: this.valueColorByClass(element, value) },
              geometry: {
                type: 'Polygon',
                coordinates: [coordinates],
              },
            }

            features.push(feature)
          }
        }
      }

      return {
        type: 'FeatureCollection',
        features,
      }
    },

    createGrid(measurements, boundaries, cellSizeLat, cellSizeLng) {
      let { minLat, maxLat, minLng, maxLng } = boundaries
      minLat -= cellSizeLat
      maxLat += cellSizeLat
      minLng -= cellSizeLng
      maxLng += cellSizeLng
      const category = measurements[0].category

      const numRows = Math.max(1, Math.ceil(Math.abs(maxLat - minLat) / cellSizeLat))
      const numCols = Math.max(1, Math.ceil(Math.abs(maxLng - minLng) / cellSizeLng))

      const features: Feature<Polygon>[] = []

      const gridValues: number[][] = Array.from({ length: numRows }, () => Array(numCols).fill(undefined))

      for (const measure of measurements) {
        const location = this.locationById(measure.locationId)
        if (location === undefined) continue
        const { lat, lng } = location.coordinates
        const i = Math.floor((lat - minLat) / cellSizeLat)
        const j = Math.floor((lng - minLng) / cellSizeLng)
        // console.debug(lat, lng, i, j)
        if (gridValues[i][j] === undefined) {
          gridValues[i][j] = measure.value
        } else {
          gridValues[i][j] = Math.max(gridValues[i][j], measure.value)
        }
      }
      //console.debug(gridValues)

      let gridCells = []
      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          if (gridValues[i][j]) {
            gridCells.push({
              x: i,
              y: j,
              value: gridValues[i][j],
            })
          }
        }
      }

      const interpolatedValues = this.inverseDistanceWeighted(gridCells, numRows, numCols, 8)
      //console.debug(interpolatedValues)

      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          const lat1 = minLat + i * cellSizeLat
          const lat2 = lat1 + cellSizeLat
          const lng1 = minLng + j * cellSizeLng
          const lng2 = lng1 + cellSizeLng

          const coordinates: Position[] = [
            [lng1, lat1],
            [lng1, lat2],
            [lng2, lat2],
            [lng2, lat1],
            [lng1, lat1],
          ]

          let value = interpolatedValues[i][j]

          if (value > 0) {
            const feature: Feature<Polygon> = {
              type: 'Feature',
              properties: { value: value, name: category },
              geometry: {
                type: 'Polygon',
                coordinates: [coordinates],
              },
            }

            features.push(feature)
          }
        }
      }

      return {
        type: 'FeatureCollection',
        features,
      }
    },

    createGridFullSize(category: string): FeatureCollection<Polygon> {
      const { locationZoneLat, locationZoneLng } = this.currentProject
      const [minLat, maxLat] = locationZoneLat
      const [minLng, maxLng] = locationZoneLng
      const gridSize = 0.05

      const numRows = Math.ceil(Math.abs(maxLat - minLat) / gridSize)
      const numCols = Math.ceil(Math.abs(maxLng - minLng) / gridSize)

      const features: Feature<Polygon>[] = []
      const thLaboMeasurements = this.measurementsByCategory(category).map((measure) => {
        measure.location = this.locationById(measure.locationId)
        return measure
      })
      //console.debug(thLaboMeasurements)

      const gridValues: number[][] = Array.from({ length: numRows }, () => Array(numCols).fill(undefined))

      for (const measure of thLaboMeasurements) {
        const { lat, lng } = measure.location.coordinates
        const i = Math.floor((lat - minLat) / gridSize)
        const j = Math.floor((lng - minLng) / gridSize)
        if (gridValues[i][j] === undefined) {
          gridValues[i][j] = measure.value
        } else {
          gridValues[i][j] = Math.max(gridValues[i][j], measure.value)
        }
      }
      //console.debug(gridValues)

      let gridCells = []
      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          if (gridValues[i][j]) {
            gridCells.push({
              x: i,
              y: j,
              value: gridValues[i][j],
            })
          }
        }
      }

      const interpolatedValues = this.inverseDistanceWeighted(gridCells, numRows, numCols, 8)
      //console.debug(interpolatedValues)

      for (let i = 0; i < numRows; i++) {
        for (let j = 0; j < numCols; j++) {
          const lat1 = minLat + i * gridSize
          const lat2 = lat1 + gridSize
          const lng1 = minLng + j * gridSize
          const lng2 = lng1 + gridSize

          const coordinates: Position[] = [
            [lng1, lat1],
            [lng1, lat2],
            [lng2, lat2],
            [lng2, lat1],
            [lng1, lat1],
          ]

          let value = interpolatedValues[i][j]

          if (value > 0) {
            const feature: Feature<Polygon> = {
              type: 'Feature',
              properties: { value: value, name: category },
              geometry: {
                type: 'Polygon',
                coordinates: [coordinates],
              },
            }

            features.push(feature)
          }
        }
      }

      return {
        type: 'FeatureCollection',
        features,
      }
    },
    inverseDistanceWeighted(points, dimx, dimy, power) {
      var matrix = []
      for (var i = 0; i < dimx; i++) {
        matrix[i] = []
        for (var j = 0; j < dimy; j++) {
          const val = this.calcVal({ x: i, y: j }, points, power)
          matrix[i].push(val)
        }
      }
      return matrix
    },
    calcVal(v, points, p) {
      var top = 0
      var bot = 0

      for (var i = 0; i < points.length; i++) {
        const dist = this.calcDist(v, points[i])
        if (dist < 1.0) return points[i].value
        // console.debug(dist)
        // if (dist > 5.0) continue
        top += points[i].value / Math.pow(dist, p)
        bot += 1 / Math.pow(dist, p)
      }
      if (bot == 0) {
        return top
      } else {
        return top / bot
      }
    },
    calcDist(p, q) {
      return Math.sqrt(Math.pow(q.x - p.x, 2) + Math.pow(p.y - q.y, 2))
    },
    classFromElementValue(element: string, value: number | number[][]) {
      const v: number = typeof value === 'number' ? value : Math.max(...value[0])
      switch (element.toUpperCase()) {
        case 'H2':
          return v > 200 ? 4 : v > 100 ? 3 : v > 50 ? 2 : v > 10 ? 1 : 0
        case 'HE':
          return v > 5.24 ? 1 : 0
        case 'O2':
          return v > 20 ? 4 : v > 15 ? 3 : v > 10 ? 2 : v > 5 ? 1 : 0
        case 'CO':
          return v > 4 ? 4 : v > 3 ? 3 : v > 2 ? 2 : v > 1 ? 1 : 0
        case 'CO2':
          return v > 10 ? 2 : v > 5 ? 1 : 0
        case 'CH4':
          return v > 0.1 ? 2 : v > 0.1 ? 1 : 0
        case 'N2':
          return v > 100 ? 0 : v > 90 ? 3 : v > 80 ? 2 : v >= 70 ? 1 : 0
        default:
          break
      }
      return v > 200 ? 4 : v > 100 ? 3 : v > 50 ? 2 : v > 10 ? 1 : 0
    },

    async populateLkpDataFromCsv(csv: File, completeCallback?: Function) {
      //console.debug(csv)
      const me = this
      let importStats = {
        location: 0,
        cluster: 0,
        measurement: 0,
      }

      const measureCorrespondance = [
        {
          inHeader: 'H2 GA5000 (ppm)',
          outCategory: 'H2-GA5000',
          outElement: 'H2',
          outTool: 'GA5000',
          outUnits: ['ppm'],
          outLabel: 'H2 GA5000',
        },
        {
          inHeader: 'O2%',
          outCategory: 'O2-GA5000',
          outElement: 'O2',
          outTool: 'GA5000',
          outUnits: ['%'],
          outLabel: 'O2 GA5000',
        },
        {
          inHeader: 'CO2%',
          outCategory: 'CO2-GA5000',
          outElement: 'CO2',
          outTool: 'GA5000',
          outUnits: ['%'],
          outLabel: 'CO2 GA5000',
        },
        {
          inHeader: 'H2S_ppm',
          outCategory: 'H2S-GA5000',
          outElement: 'H2S',
          outTool: 'GA5000',
          outUnits: ['ppm'],
          outLabel: 'H2S GA5000',
        },
        {
          inHeader: 'CO_ppm',
          outCategory: 'CO-GA5000',
          outElement: 'CO',
          outTool: 'GA5000',
          outUnits: ['ppm'],
          outLabel: 'CO GA5000',
        },
        {
          inHeader: 'CH4_%',
          outCategory: 'CH4-GA5000',
          outElement: 'CH4',
          outTool: 'GA5000',
          outUnits: ['%'],
          outLabel: 'CH4 GA5000',
        },
        {
          inHeader: 'N2_%',
          outCategory: 'N2-GA5000',
          outElement: 'N2',
          outTool: 'GA5000',
          outUnits: ['%'],
          outLabel: 'N2 GA5000',
        },
        {
          inHeader: 'H2 Variotec (ppm)',
          outCategory: 'H2-Variotec',
          outElement: 'H2',
          outTool: 'Variotec',
          outUnits: ['ppm'],
          outLabel: 'H2 Variotec',
        },
        {
          inHeader: 'H2 labo (ppm)',
          outCategory: 'H2-Labo',
          outElement: 'H2',
          outTool: 'Labo',
          outUnits: ['ppm'],
          outLabel: 'H2 Labo',
        },
        {
          inHeader: 'CO2 labo',
          outCategory: 'CO2-Labo',
          outElement: 'CO2',
          outTool: 'Labo',
          outUnits: ['ppm'],
          outLabel: 'CO2 Labo',
        },
        {
          inHeader: 'He labo (ppm)',
          outCategory: 'He-Labo',
          outElement: 'He',
          outTool: 'Labo',
          outUnits: ['ppm'],
          outLabel: 'He Labo',
        },
      ]

      const globalStore = useGlobalStore()
      globalStore.currentProjectName = 'Natural H2 Brittany'
      globalStore.currentProjectIndex = 1
      me.currentDataset = 1

      // Reset project data
      me.currentProject.locations = []
      me.currentProject.clusters = []
      me.currentProject.measurements = []

      // Populate with new data
      const promise = Papa.parse(csv, {
        header: true,
        delimiter: ';',
        worker: true,
        complete: function (results, file) {
          console.debug('Parsing complete:', results, file)
          me.currentProject.isFakeDataset = false
          console.debug(me.currentProject)
          if (completeCallback) {
            completeCallback(importStats)
          }
        },
        step: function (result, i) {
          // do stuff with result
          //console.debug('Parsing CSV:', result, i)

          const { id, LAT, LONG, Date_heure, ZONE, Sol, Unite_geol, Variotec_long, CommentR } = result.data

          if (LAT && LONG && ZONE) {
            const lat = parseFloat(LAT.replace(',', '.'))
            const lng = parseFloat(LONG.replace(',', '.'))
            let locationId = uuidv4()
            let location = me.currentProject.locations.find(
              (loc) => Math.abs(loc.coordinates.lat - lat) < 0.00001 && Math.abs(loc.coordinates.lng - lng) < 0.00001,
            )

            if (!location) {
              location = {
                id: locationId,
                name: `Location ${me.currentProject.locations.length + 1}`,
                coordinates: {
                  lat: lat,
                  lng: lng,
                },
                soilType: Sol != '' ? Sol : 'Unknown',
                geologicalUnit: Unite_geol != '' ? Unite_geol : 'Unknown',
              }

              me.currentProject.locations.push(location)
              importStats.location++
            } else {
              locationId = location.id
            }

            //console.debug(result.data['CH4_%'])

            measureCorrespondance.forEach((params) => {
              if (result.data[params.inHeader] !== '' && result.data[params.inHeader] !== undefined) {
                const measureValue = parseFloat(result.data[params.inHeader].replace(',', '.'))
                me.currentProject.measurements.push({
                  id: uuidv4(),
                  name: `${params.outLabel} ${me.currentProject.measurements.length + 1}`,
                  category: params.outCategory,
                  element: params.outElement,
                  tool: params.outTool,
                  locationId: location.id,
                  description: CommentR !== undefined ? CommentR : '',
                  sampleId: id !== undefined ? id : '',
                  acquisitionDate: Date_heure,
                  value: measureValue,
                  valueClass: me.classFromElementValue(params.outElement, measureValue),
                  units: params.outUnits,
                })
                importStats.measurement++
              }
            })

            if (Variotec_long) {
              const measureValues = Variotec_long.split(' ').map((rawVal) => parseFloat(rawVal.replace(',', '.')))
              const measureInterval = measureValues.map((_, i) => i + 1)
              me.currentProject.measurements.push({
                id: uuidv4(),
                name: `Monitoring ${me.currentProject.measurements.length + 1}`,
                category: 'H2-Monitoring',
                element: 'H2',
                tool: 'Monitoring',
                locationId: location.id,
                description: CommentR !== undefined ? CommentR : '',
                sampleId: id !== undefined ? id : '',
                acquisitionDate: Date_heure,
                value: [measureValues, measureInterval],
                valueClass: me.classFromElementValue('H2', [measureValues, measureInterval]),
                units: ['ppm', 'seconds'],
              })
              importStats.measurement++
            }

            if (ZONE) {
              let zone = me.currentProject.clusters.find((cluster) => cluster.name === ZONE)

              if (!zone) {
                zone = {
                  id: uuidv4(),
                  name: ZONE,
                  locations: [],
                }

                me.currentProject.clusters.push(zone)
                importStats.cluster++
              }

              if (!zone.locations.includes(locationId)) {
                zone.locations.push(locationId)
              }
            }
          }
        },
        error: function (error) {
          console.debug('Parsing error:', error)
        },
      })

      return promise
    },
  },
})
