import { defineStore } from 'pinia'
import { interpolateRgb, interpolateRgbBasis } from 'd3-interpolate'
import { interpolateViridis, interpolateRainbow } from 'd3-scale-chromatic'

export const useColorScale = defineStore('colorScale', {
  state: () => {
    return {
      c1: interpolateRgb('darkgreen', 'gold'),
      c2: interpolateRgb('purple', 'orange'),
      c3: interpolateRgb('steelblue', 'brown'),
      h2: interpolateRgbBasis(['#3c40fb', '#0047da', '#0047b5', '#00438f', '#4c458e', '#ae4190', '#ef5165', '#f7931e']),
      h2Class: ['#2643ef', '#0045a2', '#7f438f', '#d74b75', '#f68a28'],
      heClass: ['#219a7c', '#e6c477'],
      coClass: ['#32ddff', '#00c3ff', '#00a5ff', '#0079ff', '#211fff'],
      o2Class: ['#fff3d4', '#ffd794', '#ffb060', '#ff7d2d', '#ff3e0e'],
      co2Class: ['#a8fddf', '#5d876c', '#172416'],
      ch4Class: ['#fffa26', '#f7bfbe', '#a071ac'],
      n2Class: ['#ffffff', '#ffbcfc', '#fe71bf', '#f61a7d'],
      cluster: interpolateRgbBasis([
        '#f38181',
        '#ea86c6',
        '#b09ff7',
        '#51b8fc',
        '#00c5d3',
        '#5bc899',
        '#a6c06f',
        '#e1b172',
      ]),
      soil: interpolateViridis,
    }
  },

  getters: {
    classColors(state) {
      return [
        {
          displayName: 'H2 (ppm)',
          element: 'H2',
          colorScale: [
            { label: 'Class 1', range: '0-10 ppm', color: state.h2Class[0] },
            { label: 'Class 2', range: '10-50 ppm', color: state.h2Class[1] },
            { label: 'Class 3', range: '50-100 ppm', color: state.h2Class[2] },
            { label: 'Class 4', range: '100-200 ppm', color: state.h2Class[3] },
            { label: 'Class 5', range: '>200 ppm', color: state.h2Class[4] },
          ],
        },
        {
          displayName: 'He (ppm)',
          element: 'He',
          colorScale: [
            { label: 'Class 1', range: '<5.24 ppm', color: state.heClass[0] },
            { label: 'Class 2', range: '>5.24 ppm', color: state.heClass[1] },
          ],
        },
        {
          displayName: 'CO (ppm)',
          element: 'CO',
          colorScale: [
            { label: 'Class 1', range: '0-1 ppm', color: state.coClass[0] },
            { label: 'Class 2', range: '1-2 ppm', color: state.coClass[1] },
            { label: 'Class 3', range: '2-3 ppm', color: state.coClass[2] },
            { label: 'Class 4', range: '3-4 ppm', color: state.coClass[3] },
            { label: 'Class 5', range: '4-5 ppm', color: state.coClass[4] },
          ],
        },
        {
          displayName: 'O2 (%)',
          element: 'O2',
          colorScale: [
            { label: 'Class 1', range: '0-5 %', color: state.o2Class[0] },
            { label: 'Class 2', range: '5-10 %', color: state.o2Class[1] },
            { label: 'Class 3', range: '10-15 %', color: state.o2Class[2] },
            { label: 'Class 4', range: '15-20 %', color: state.o2Class[3] },
            { label: 'Class 5', range: '20-25 %', color: state.o2Class[4] },
          ],
        },
        {
          displayName: 'CO2 (%)',
          element: 'CO2',
          colorScale: [
            { label: 'Class 1', range: '0-5 %', color: state.co2Class[0] },
            { label: 'Class 2', range: '5-10 %', color: state.co2Class[1] },
            { label: 'Class 3', range: '10-15 %', color: state.co2Class[2] },
          ],
        },
        {
          displayName: 'N2 (%)',
          element: 'N2',
          colorScale: [
            { label: 'Invalid', range: '', color: state.n2Class[0] },
            { label: 'Class 1', range: '70-80 %', color: state.n2Class[1] },
            { label: 'Class 2', range: '80-90 %', color: state.n2Class[2] },
            { label: 'Class 3', range: '90-100 %', color: state.n2Class[3] },
          ],
        },
        {
          displayName: 'CH4 (%)',
          element: 'CH4',
          colorScale: [
            { label: 'Class 1', range: '0 %', color: state.ch4Class[0] },
            { label: 'Class 2', range: '0.1 %', color: state.ch4Class[1] },
            { label: 'Class 3', range: '0.2 %', color: state.ch4Class[2] },
          ],
        },
      ]
    },
  },

  actions: {},
})
