<template>
  <VaSidebar :width="sidebarWidth" :color="color" minimized-width="0">
    <VaAccordion>
      <VaCollapse>
        <template #header="{ value: isCollapsed }">
          <VaSidebarItem :active="true" :active-color="activeColor" text-color="primary" hover-opacity="0.10">
            <VaSidebarItemContent class="py-2 pr-2 pl-2">
              <VaSidebarItemTitle class="flex justify-between items-center leading-5 font-semibold cursor-auto">
                Contextual Actions
                <VaButton
                  preset="secondary"
                  @click="isRightSidebarMinimized = !isRightSidebarMinimized"
                  class="cursor-pointer"
                  icon="keyboard_arrow_right"
                  v-tooltip="'Close right panel'"
                >
                </VaButton>
              </VaSidebarItemTitle>
            </VaSidebarItemContent>
          </VaSidebarItem>
        </template>
      </VaCollapse>
    </VaAccordion>

    <VaAlert v-if="currentMeasurements === undefined" color="info" border="left" class="m-2">
      Click on any measurement in the map to display more information!
    </VaAlert>
    <div v-else class="w-full p-2">
      <h3 class="sidebar-title mb-2"><VaIcon name="info" size="22px" /> Selected measurements</h3>

      <template v-for="m in currentMeasurements">
        <div>{{ m.element }} {{ m.tool }}</div>
        <div>{{ formatMeasurementSampleId(m) }}</div>
        <div>
          {{ formatMeasurementValue(m) }}
          <span class="small-color-swatch" :style="{ backgroundColor: formatMeasurementColor(m) }"></span>
        </div>
        <div>{{ formatMeasurementDate(m) }}</div>
        <div>{{ formatMeasurementDescription(m) }}</div>
        <div>{{ formatMeasurementLocation(m) }}</div>
        <div class="flex items-center justify-between">
          <VaButton
            icon="my_location"
            size="small"
            class="mr-1"
            preset="secondary"
            v-tooltip="'Coming soon: automatically zoom in the selected measurement in the map'"
            >Show in map
          </VaButton>
        </div>
        <VaDivider />
      </template>
    </div>

    <VaDivider />
    <div class="flex w-full p-1">
      <h3 class="sidebar-title mb-2"><VaIcon name="palette" size="22px" /> Legend</h3>
    </div>
    <div class="flex w-full p-1">
      <ColorScales></ColorScales>
    </div>
    <VaDivider />
  </VaSidebar>
</template>

<style lang="scss" scoped>
.small-color-swatch {
  width: 10px;
  height: 10px;
  display: inline-block;
  border: solid 1px #000;
}

.sidebar-title {
  font-weight: bold;
}

.date-slider {
  padding-left: 60px;
  padding-right: 60px;
}

#project-browser.grtv-wrapper {
  .grtvn-self-text,
  .grtvn-self-label {
    /* margin-left: 10px; */
  }

  .grtvn-children-wrapper {
    margin-left: 20px;
  }
}

.va-file-upload--dropzone {
  overflow: visible;
}
</style>

<script lang="ts">
import { defineComponent, watch, ref, computed, nextTick } from 'vue'
import { useRoute } from 'vue-router'

import { useI18n } from 'vue-i18n'
import { useToast, useColors, VaIcon } from 'vuestic-ui'

import { TreeView } from '@grapoza/vue-tree'

import ColorScales from '@components/ColorScales.vue'

import VaIconMenuCollapsed from '@components/icons/VaIconMenuCollapsed.vue'
import navigationRoutes, { type INavigationRoute } from './NavigationRoutes'
import ContextMenu from '@imengyu/vue3-context-menu'

import { useProjectBrowserStore } from '@stores/project-browser'
import { useLkpDataStore } from '@stores/geoscientific-project'
import { v4 as uuidv4 } from 'uuid'
import { storeToRefs } from 'pinia'
import { useGlobalStore } from '@stores/global-store'
import { Measurement } from '@data/types'

export default defineComponent({
  name: 'ContextualSidebar',
  components: {
    TreeView,
    ColorScales,
    VaIconMenuCollapsed,
  },
  props: {
    visible: { type: Boolean, default: true },
    mobile: { type: Boolean, default: false },
  },
  emits: ['update:visible'],
  inject: {
    emitter: 'emitter',
  },
  setup: (props, { emit }) => {
    const { getColor, colorToRgba } = useColors()
    const route = useRoute()
    const { t } = useI18n()

    const sidebarWidth = computed(() => (props.mobile ? '100vw' : '280px'))
    const color = computed(() => getColor('background-secondary'))
    const activeColor = computed(() => colorToRgba(getColor('focus'), 0.1))

    const globalStore = useGlobalStore()

    const { isRightSidebarMinimized } = storeToRefs(globalStore)

    return {
      sidebarWidth,
      color,
      activeColor,
      navigationRoutes,
      t,
      isRightSidebarMinimized,
    }
  },
  data() {
    const dataStore = useLkpDataStore()
    let currentMeasurements: Measurement[] | undefined = undefined

    return {
      dataStore,
      currentMeasurements,
    }
  },
  mounted() {
    this.emitter.on('lkp-measurement-selected', this.displayMeasurementInfo)
    this.emitter.on('lkp-database-changed', this.clearInfo)
  },
  unmounted() {
    this.emitter.off('lkp-measurement-selected', this.displayMeasurementInfo)
    this.emitter.off('lkp-database-changed', this.clearInfo)
  },
  methods: {
    displayMeasurementInfo(options) {
      console.debug('Display measurement info in right panel...', options)

      this.currentMeasurements = undefined
      if (options.id !== undefined) {
        const m = this.dataStore.measurementById(options.id)
        this.currentMeasurements = [m]
      } else if (options.ids !== undefined) {
        this.currentMeasurements = options.ids.map((id) => this.dataStore.measurementById(id))
      }
    },
    clearInfo() {
      console.debug('Clear measurement info in right panel...')

      this.currentMeasurements = undefined
    },
    formatMeasurementDate(measure) {
      const acquisitionDate = new Date(measure.acquisitionDate)
      if (isNaN(acquisitionDate.valueOf())) return 'Unknown date'
      return `Acquisition date: ${acquisitionDate.toLocaleString()}`
    },
    formatMeasurementSampleId(measure) {
      return measure.sampleId ? `Sample ID: ${measure.sampleId}` : 'Unknown sample ID'
    },
    formatMeasurementValue(measure) {
      const prefix = Array.isArray(measure.value) ? 'Max value: ' : 'Value: '
      const val = Array.isArray(measure.value) ? Math.max(...measure.value[0]) : measure.value
      const valString = val.toLocaleString(undefined, { minimumFractionDigits: 2 })
      const suffix = measure.valueClass ? `(class ${measure.valueClass + 1})` : ''
      return `${prefix}${valString} ${measure.units[0]} ${suffix}`
    },
    formatMeasurementColor(measure) {
      return this.dataStore.classColor(measure)
    },
    formatMeasurementDescription(measure) {
      return measure.description ?? 'No description'
    },
    formatMeasurementLocation(measure) {
      const location = this.dataStore.locationById(measure.locationId)
      if (location === undefined) return 'Location unknown'

      const cluster = this.dataStore.clusterByLocationId(measure.locationId)
      const clusterString = cluster !== undefined ? cluster.name : 'Unknown cluster'

      const latString = location.coordinates.lat.toLocaleString(undefined, { minimumFractionDigits: 2 })
      const lngString = location.coordinates.lng.toLocaleString(undefined, { minimumFractionDigits: 2 })
      return `Location (lat/long): ${latString}/${lngString} (${clusterString})`
    },
  },
})
</script>
